.taboo.item {
  min-width: min-content;
}
.taboo.item .window-content search {
  display: none;
}
.taboo.item .window-content {
  max-height: 90vh;
  display: grid;
  gap: 0.5em;
  grid: "desc header header" fit-content(0) "desc deet deet" fit-content(40vh) "adv  eff act" fit-content(15vh) ".  . tabs" auto/400px 400px 300px;
}
.taboo.item .window-content [data-application-part=header] {
  grid-area: header;
}
.taboo.item .window-content [data-application-part=tabs] {
  grid-area: tabs;
}
.taboo.item .window-content nav [data-tab=description], .taboo.item .window-content nav [data-tab=details] {
  display: none;
}
.taboo.item .window-content hr.ampersand {
  display: none;
}
.taboo.item .window-content .tab:not(.contents) {
  display: flex;
}
.taboo.item .window-content .tab:not(.contents)[data-tab=description] {
  grid-area: desc;
}
.taboo.item .window-content .tab:not(.contents)[data-tab=details] {
  grid-area: deet;
}
.taboo.item .window-content .tab:not(.contents)[data-tab=activities] {
  grid-area: act;
}
.taboo.item .window-content .tab:not(.contents)[data-tab=effects] {
  grid-area: eff;
}
.taboo.item .window-content .tab:not(.contents)[data-tab=advancement] {
  grid-area: adv;
}

.taboo.activity {
  min-width: fit-content;
}
.taboo.activity .window-content {
  display: grid;
  grid-template-columns: repeat(3, 300px);
  grid-template-rows: repeat(auto-fill, minmax(50px, auto));
  grid-auto-flow: dense;
}
.taboo.activity .window-content .tab {
  overflow-y: auto;
  display: flex;
}
.taboo.activity .window-content .tabs {
  display: none;
}

.active-effect-config .editor {
  height: 300px;
}
.active-effect-config .changes {
  gap: 0;
}
.active-effect-config .changes .key {
  margin-block-start: 0.5em;
  grid-column: 1/-1;
}
.active-effect-config .changes [data-changes] {
  grid-template-columns: max-content auto 6ch 3ch;
  gap: 0.2em;
}